import React from "react";
import "./ProjectBlock.scss";

const ProjectBlock = (props) => {
    const {title, description, link, linkText, image} = props;

    const nodesc = () => {
        return(
            <p className="block-p no-flex">Various maintanence and upgrades to a number of different sites including <a href='https://precisionresourcegroup.co.uk/'>PRG Recruitment</a>, <a href='https://therecruitmentco.uk/'>The Recruitment Co</a> , and <a href='https://careers.domesticandgeneral.com/'>Domestic and General Recruitment</a>, and more.</p>
        )
    }

    return(
        <div className ="block">
            <h1 className="block-title">{title}</h1>
            <div className="grid">  
                <div className="grid-text"> 
                {description !== '' ? (        
                <p className="block-p">{description}</p>
                ) : (nodesc())} 
                {link !== undefined ? (
                <a className="site-link" href={link}>{linkText}</a>
                ) : (null)} 
                </div>             
                <div className="media">
                    <img className="image" alt="" src={image}/>
                </div>    
            </div>   
        </div>        
    )
}

export default ProjectBlock;